	module.exports = {"data":{"nodePost":{"title":"Happy Holidays","body":{"value":"<p>I'm enjoying my traditional holiday illness this year. Without fail, once reach holiday break my defenses shut down and I catch a cold (or worse.) I'm thinking next year I'm going to take the week before Christmas off and just live in one of those plastic bubbles.</p>\r\n<p>Wishing everyone a happy and healthy holiday season.</p>"},"relationships":{"image":{"localFile":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":0.7226378498198948,"src":"/static/b052a144cb5a7e30624736be2995ff7e-1c8cbeb3e4b2d50cf094ccb416d3e79d-42807.jpg","srcSet":"/static/b052a144cb5a7e30624736be2995ff7e-1c8cbeb3e4b2d50cf094ccb416d3e79d-960f9.jpg 313w,\n/static/b052a144cb5a7e30624736be2995ff7e-1c8cbeb3e4b2d50cf094ccb416d3e79d-c36f2.jpg 625w,\n/static/b052a144cb5a7e30624736be2995ff7e-1c8cbeb3e4b2d50cf094ccb416d3e79d-42807.jpg 1250w,\n/static/b052a144cb5a7e30624736be2995ff7e-1c8cbeb3e4b2d50cf094ccb416d3e79d-dbc5e.jpg 1875w,\n/static/b052a144cb5a7e30624736be2995ff7e-1c8cbeb3e4b2d50cf094ccb416d3e79d-6e234.jpg 2500w,\n/static/b052a144cb5a7e30624736be2995ff7e-1c8cbeb3e4b2d50cf094ccb416d3e79d-19574.jpg 2608w","sizes":"(max-width: 1250px) 100vw, 1250px"}}}}},"created":1545665583}},"pathContext":{"slug":"/post/happy-holidays/"}}

